<template>
	<div class="tabs clearfix">
		<div class="tab-item fl" v-for="(tab,index) in tabs" v-text="tab.tabtxt" :class="{active: tab.active}" @click="handleClick(tab)"></div>
	</div>
</template>

<script>
	export default {
		props: {
			tabs: {
				type: Array,
				required:true
			}
		},
		methods: {
			handleClick(tab){
				tab.active || this.$emit('changeTab',tab)
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	@import "../../assets/styles/mixin.scss";
	.tabs{
		height: 60px;
		background: #FFFFFF;
		.tab-item{
			line-height: 60px;
			height: 60px;
			white-space: nowrap;
			min-width: 120px;
			text-align: center;
			font-size: 16px;
			color: #A5A8A8;
			box-sizing: border-box;
			&.active{
				color: $color-text;
				border-bottom: 2px solid $color-text;
			}
		}
	}
</style>
