<template>
	<div class="wrap">
    <TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
		<div class="orderBox">
			<TheOrderList :message='filtermessage'></TheOrderList>
		</div>
	</div>
</template>

<script>
import TheOrderList from '@/components/ThePersonCenter/TheOrderList'
import TheCompiler from '@/components/ThePersonCenter/TheCompiler'
export default {

  data () {
    return {
      tabs: [
        {
          tabtxt: '已出单',
          active: false,
					status: 3
        },
        {
          tabtxt: '保障中',
          active: true,
					status: 4
        },
        {
          tabtxt: '全部',
          active: false,
					status: -1
        }
      ],
			message: []
    }
  },
  methods: {
    handleChangeTab (item) {
      this.tabs.find(tab => tab !== item && tab.active).active = false
      item.active = true
    }
  },
	computed: {
		filtermessage(){
			let status = this.tabs.find(tab=>tab.active).status;
			if(status == -1){
				return this.message;
			}else{
				return this.message.filter(item=>item.status==status)
			}
		}
	},
  components: {
    TheOrderList,
    TheCompiler
  },
	created(){
		let message = []
		this.$api.personCenter.getIndexOrderList({
			page: 1,
			limit: 2,
			status: 0
		})
		.then((data)=>{
			console.log(data)
			this.message = JSON.parse(JSON.stringify(data.data.dataGrid.data))
		})
	}
}
</script>

<style lang="scss" scoped>
.wrap{
	box-shadow: 0px 2px 4px 0 rgba(219,219,219,.35);
	.orderBox{
		border-top: 1px solid #F1F4F4;
	}
}
</style>
